import React, { useEffect, useRef, useState } from "react";
import Peer from "simple-peer";
import { db } from "../firebase/firebase";
import { ref, push, onChildAdded } from "firebase/database";

function VideoChatRoom({ roomId, userId }) {
  const localVideoRef = useRef();
  const remoteVideoRef = useRef();
  const peerRef = useRef();
  const [stream, setStream] = useState(null);
  const [isStarted, setIsStarted] = useState(false);

  useEffect(() => {
    if (!isStarted || !stream) return;

    const signalsRef = ref(db, `rooms/${roomId}/signals`);
    const initiatorCheckRef = ref(db, `rooms/${roomId}/initiator`);

    let isInitiator = false;

    // lk kullancy belirle
    onChildAdded(initiatorCheckRef, snapshot => {
      isInitiator = false;
    });

    push(initiatorCheckRef, userId).then(() => {
      isInitiator = true;
      startPeer(stream, isInitiator, signalsRef);
    });

    return () => {
      if (peerRef.current) {
        peerRef.current.destroy();
      }
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [isStarted, stream]);

  function startMedia() {
    navigator.mediaDevices
      .getUserMedia({ video: true, audio: true })
      .then(mediaStream => {
        setStream(mediaStream);
        if (localVideoRef.current) {
          localVideoRef.current.srcObject = mediaStream;
        }
        setIsStarted(true);
      })
      .catch(err => {
        console.error("Kamera/mikrofon eriim hatas:", err);
        alert("Kamera ve mikrofon eriimini ltfen izin veriniz.");
      });
  }

  function startPeer(mediaStream, isInitiator, signalsRef) {
    const peer = new Peer({
      initiator: isInitiator,
      trickle: true, // NEML: Balantnn sorunsuz kurulmas iin
      stream: mediaStream,
      config: {
        iceServers: [
          { urls: "stun:stun.l.google.com:19302" },
          {
            urls: "turn:global.relay.metered.ca:443",
            username: "openai",
            credential: "openai"
          }
        ]
      }
    });

    peerRef.current = peer;

    peer.on("signal", data => {
      console.log("Signal gnderildi:", data);
      push(signalsRef, {
        from: userId,
        signal: data
      });
    });

    onChildAdded(signalsRef, snapshot => {
      const msg = snapshot.val();
      if (msg.from !== userId) {
        console.log("Kardan signal geldi:", msg);
        peer.signal(msg.signal);
      }
    });

    peer.on("stream", remoteStream => {
      console.log("Kar tarafn yayn geldi.");
      if (remoteVideoRef.current) {
        remoteVideoRef.current.srcObject = remoteStream;
      }
    });

    peer.on("error", err => {
      console.error("Peer error:", err);
    });
  }

  return (
    <div>
      {!isStarted && (
        <button onClick={startMedia}>Kameray A ve zin Ver</button>
      )}
      {isStarted && (
        <div>
          <h3>Sen</h3>
          <video
            ref={localVideoRef}
            muted
            autoPlay
            playsInline
            style={{ width: 300 }}
          />
          <h3>Kar taraf</h3>
          <video
            ref={remoteVideoRef}
            autoPlay
            playsInline
            style={{ width: 300 }}
          />
        </div>
      )}
    </div>
  );
}

export default VideoChatRoom;
