import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Routes, Route, useParams, useNavigate } from "react-router-dom";
import VideoChatRoom from "./components/VideoChatRoom";
import { auth, loginAnonymously } from './firebase/firebase';
import { onAuthStateChanged } from "firebase/auth";

function RoomWrapper({ user }) {
  const { roomId } = useParams(); // URL'den roomId'yi al
  return <VideoChatRoom roomId={roomId} userId={user.uid} />;
}

function Home({ user }) {
  const [inputRoomId, setInputRoomId] = useState("");
  const navigate = useNavigate();

  // Kullancnn girdii oda ID'si ile odaya ynlendirme
  const handleJoin = () => {
    if (inputRoomId.trim() !== "") {
      navigate(`/room/${inputRoomId.trim()}`);
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      <h2>Grntl Odaya Katl veya Oda Olutur</h2>
      <input
        type="text"
        placeholder="Oda ID gir veya yeni oda iin bo brak"
        value={inputRoomId}
        onChange={(e) => setInputRoomId(e.target.value)}
        style={{ padding: "8px", fontSize: "16px", width: "250px" }}
      />
      <button
        onClick={handleJoin}
        style={{ marginLeft: "10px", padding: "8px 16px", fontSize: "16px" }}
      >
        Katl
      </button>
      <p style={{ marginTop: "20px", fontStyle: "italic" }}>
        Yeni oda oluturmak iin bo brakp Enter tuuna bas veya URL'ye yeni bir ID ile git.
      </p>
    </div>
  );
}

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, currentUser => {
      if (currentUser) {
        setUser(currentUser);
        setLoading(false);
      } else {
        loginAnonymously()
          .catch(err => {
            console.error("Anonim giri hatas:", err);
            setError(err.message);
            setLoading(false);
          });
      }
    });

    return () => unsubscribe();
  }, []);

  if (loading) return <div>Giri yaplyor...</div>;
  if (error) return <div>Hata: {error}</div>;
  if (!user) return <div>Kullanc bulunamad!</div>;

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Home user={user} />} />
        <Route path="/room/:roomId" element={<RoomWrapper user={user} />} />
      </Routes>
    </Router>
  );
}

export default App;
